import { Component, OnInit } from '@angular/core';
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { AppComponent } from "../app.component";
import { AppService } from "../shared/app.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Location } from "@angular/common";

@Component({
  selector: 'app-recrutement',
  templateUrl: './recrutement.component.html',
  styleUrls: ['./recrutement.component.scss']
})
export class RecrutementComponent extends AppComponent implements OnInit {

  public slidecnt: any;
  constructor(private titleService: Title,
    private metaTagService: Meta,
    activatedRoute: ActivatedRoute,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService,
    router: Router,) {

    super(activatedRoute, router, appService, location, spinner);
    this.slidecnt = 4;
  }
  public Date: any;
  public min: any;
  public Ville: any;

  ngOnInit() {
    //SEOrepo
    this.titleService.setTitle("Ohmytennis | recrutement cours de tennis");
    this.metaTagService.removeTag('name=title');
    this.metaTagService.removeTag('name=keywords');
    this.metaTagService.removeTag('name=description');
    this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | recrutement cours de tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'cours de tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'cours de tennis collectifs' });
    this.metaTagService.addTag({ name: 'keywords', content: 'stage tennis' });
    this.metaTagService.updateTag({ name: 'description', content: "Vous voulez participez à l’aventure Ohmytennis en devenant un coach de tennis, sparring partner tennis, rédacteur sur le tennis ou encore cadreur/monteur vidéo sur le tennis ? Proposez votre candidature pour intégrer rapidement notre équipe !" }, 'name="description"');
  }

}
