import { Component, OnInit } from '@angular/core';
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { AppComponent } from "../app.component";
import { AppService } from "../shared/app.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Location } from "@angular/common";
@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.scss']
})
export class FaqComponent extends AppComponent implements OnInit {

  public slidecnt: any;
  constructor(private titleService: Title,
  private metaTagService: Meta,
  activatedRoute: ActivatedRoute,
  appService: AppService,
  location: Location,
  spinner: NgxSpinnerService,
  router: Router,) {

    super(activatedRoute, router, appService, location, spinner);
    this.slidecnt = 4;
   }
  public Date: any;
  public min: any;
  public Ville: any;
  ngOnInit() {
    this.titleService.setTitle("Ohmytennis | professeur particulier de tennis");
    this.metaTagService.removeTag('name=title');
    this.metaTagService.removeTag('name=keywords');
    this.metaTagService.removeTag('name=description');
    this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | professeur particulier de tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'professeur particulier de tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'club de tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'team building tennis' });
    this.metaTagService.updateTag({ name: 'description', content: "Quelles sont les questions que vous vous posez pour avoir un professeur particulier de tennis , pour trouver un club de tennis pour des cours collectifs ou encore organiser un team building avec son entreprise pour renforcer les liens d’équipe. Nous répondons à tout cela sur Ohmytennis par le biais de notre FAQ." }, 'name="description"');
  }

}
