import { Component, OnInit } from '@angular/core';
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { AppComponent } from "../app.component";
import { AppService } from "../shared/app.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Location } from "@angular/common";

@Component({
  selector: 'app-cours-de-tennis',
  templateUrl: './cours-de-tennis.component.html',
  styleUrls: ['./cours-de-tennis.component.scss']
})
export class CoursDeTennisComponent extends AppComponent implements OnInit {
  public slidecnt: any;
  constructor(private titleService: Title,
  private metaTagService: Meta,
  activatedRoute: ActivatedRoute,
  appService: AppService,
  location: Location,
  spinner: NgxSpinnerService,
  router: Router,) {

    super(activatedRoute, router, appService, location, spinner);
    this.slidecnt = 4;
   }
  public Date: any;
  public min: any;
  public Ville: any;


  ngOnInit() {
    //SEO
    this.titleService.setTitle("Ohmytennis | cours tennis");
    this.metaTagService.removeTag('name=title');
    this.metaTagService.removeTag('name=keywords');
    this.metaTagService.removeTag('name=description');
    this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | cours tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'cours tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'cours de tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'cours individuels tennis' });
    this.metaTagService.updateTag({ name: 'description', content: "Rendez-vous sur notre plateforme communautaire pour prendre des cours de tennis avec nos profs dès maintenant. Vous pouvez assister à des cours de tennis individuels ou collectifs en adultes ou enfants pour progresser rapidement (coup droit, service, revers)." }, 'name="description"');
  }

}
