import { Component, OnInit } from '@angular/core';
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
@Component({
  selector: 'app-sitemap',
  templateUrl: './sitemap.component.html',
  styleUrls: ['./sitemap.component.scss']
})
export class SitemapComponent implements OnInit {

  constructor(private titleService: Title,
    private metaTagService: Meta) { }

  ngOnInit() { 
     //SEOrepo
     this.titleService.setTitle("Ohmytennis | plan du site");
     this.metaTagService.removeTag('name=title');
     this.metaTagService.removeTag('name=keywords');
     this.metaTagService.removeTag('name=description');
     this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | plan du site' });
  }

}
