import { Component, OnInit, AfterViewChecked } from '@angular/core';
import {
  FormsModule, FormGroup, FormControl, FormBuilder, Validators, ReactiveFormsModule, FormArray
} from '@angular/forms';
import { Location } from '@angular/common';
import { BrowserModule, Title, Meta } from '@angular/platform-browser';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { AppService } from '../shared/app.service';
import { AppComponent } from '../app.component';
/* [ Spinner ] */
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-contactnew',
  templateUrl: './contactnew.component.html',
  styleUrls: ['./contactnew.component.scss']
})
export class ContactnewComponent extends AppComponent implements OnInit {

  public contactForm: FormGroup;

  constructor(
    activatedRoute: ActivatedRoute,
    router: Router,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService,
    private formBuilder: FormBuilder,
    private titleService: Title,
    private metaTagService: Meta
  ) {
    super(
      activatedRoute,
      router,
      appService,
      location,
      spinner
    );
  }

  public Date: any;
  public min: any;
  public Ville: any;
  ngOnInit() {
    //SEO
    this.titleService.setTitle("Ohmytennis | Contact tennis");
    this.metaTagService.removeTag('name=title');
    this.metaTagService.removeTag('name=keywords');
    this.metaTagService.removeTag('name=description');
    this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | Contact tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'contact tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'question tennis' });
    // this.metaTagService.addTag({ name: 'keywords', content: 'stage tennis' });
    this.metaTagService.updateTag({ name: 'description', content: "Vous souhaitez nous poser des questions sur le site Ohmytennis ? Cette rubrique est faite pour vous répondre concernant notre offre de professeurs de tennis, nos stages de tennis ou encore vos problématiques pour trouver un sparring tennis proche de chez vous" }, 'name="description"');
    this.contactForm = this.formBuilder.group({
      court_name: ["", Validators.required],
        incharge_name: ["", Validators.required],
        court_address: ["", Validators.required],
        court_postal_code: ["", Validators.required],
        court_email: ["", [Validators.required, 
          Validators.email
         ]],
        court_phone: ["", [Validators.required, Validators.minLength(10)]],
    });
  }


  /* [ Form controls ] */
  get rForm() {
    return this.contactForm.controls;
  }

  
  numberOnly(event): boolean {
    if (event.target.value.length > 9) { return false; }
    const charCode = (event.which) ? event.which : event.keyCode;
    if ((charCode > 31) && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  addcontactnew(isValid) {
    //if (isValid.status === 'VALID') {
      

      this.spinner.show();
      this.appService.create('/user/addcontact', this.contactForm.value)
        .subscribe(response => {
          if (response && response.isSuccess == true) {
            isValid.reset();
            this._showAlertMessage('alert-success', 'coordonnées soumises avec succès');
          } else {         
            this._showAlertMessage('alert-danger', "Le champ obligatoire est manquant");  
          }
          this.spinner.hide();
        }, error => {
        });
    //}
  }
}
