import {
  Component,
  OnInit
} from "@angular/core";
import { Location } from "@angular/common";
import { ActivatedRoute, Router } from "@angular/router";
import { AppService } from "../../../../shared/app.service";
import { AppComponent } from "../../../../app.component";
import * as moment from "moment";
import { BrowserModule, Title, Meta } from "@angular/platform-browser";

/* [ Spinner ] */
import { NgxSpinnerService } from "ngx-spinner";
import { NgbDateStruct, NgbCalendar } from "@ng-bootstrap/ng-bootstrap";
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
@Component({
  selector: 'app-recherche-partenaire',
  templateUrl: './recherche-partenaire.component.html',
  styleUrls: ['./recherche-partenaire.component.scss']
})
export class RecherchePartenaireComponent extends AppComponent implements OnInit {
  public min = new Date(Date.now() - 24 * 60 * 60 * 1000);

  public Ville: any = "";
  public Date: any = "";
  coachform:FormGroup;

  constructor(
    private calendar: NgbCalendar,
    activatedRoute: ActivatedRoute,
    router: Router,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService,
    private formBuilder:FormBuilder,
    private titleService: Title,
    private metaTagService: Meta
    ) {   super(activatedRoute, router, appService, location, spinner);}

  ngOnInit() {
    this.coachform = this.formBuilder.group({
      code_postal: [null, [Validators.required]],
      Date: [null, [Validators.required]]  
    });

     //SEO
     this.titleService.setTitle("Ohmytennis | recherche de partenaires tennis");
     this.metaTagService.removeTag('name=title');
     this.metaTagService.removeTag('name=keywords');
     this.metaTagService.removeTag('name=description');

     this.metaTagService.addTag({ name: 'keywords', content: 'recherche partenaire de tennis' });
     this.metaTagService.addTag({ name: 'keywords', content: 'trouver partenaire tennis' });
     this.metaTagService.addTag({ name: 'keywords', content: 'partenaire de sport' });

     this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | recherche de partenaires tennis' });
     this.metaTagService.updateTag({ name: 'description', content: "Vous souhaitez faire une partie avec un partenaire de tennis pour faire du sport ? Le site ohmytennis.com vous propose de trouver près de chez vous un joueur ou une joueuse de votre niveau pour taper la balle sur un terrain de tennis. C e sera l’occasion de faire connaissance par le biais du sport" }, 'name="description"');
  }
  
  handleClick(event: Event) {
    sessionStorage.setItem("Ville", this.Ville ? this.Ville : "");
    sessionStorage.setItem("Date", this.Date ? this.formatDate(this.Date) : "");
    this.router.navigate(["/ohmycoachnew"]);
  }

  onSubmitCoach()
  {
    var codepostal = this.coachform.value.code_postal;
   
    if(codepostal)
    {
     
      localStorage.setItem("codepostal", codepostal);
    }
    else
    {
      
      localStorage.removeItem("codepostal");
    }
    
    this.router.navigate(["/ohmycoachnew"]);

  }
  formatDate(date) {
    var d = new Date(date),
      month = "" + (d.getMonth() + 1),
      day = "" + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;

    return [year, month, day].join("-");
  }

}
